@import "../../styles/helpers/_helpers";

.header {
	grid-column: 1/-1;
}
.header__top {
	display: grid;
	gap: var(--block-spacing) var(--grid-gap);
	grid-template-columns: 1fr repeat(var(--grid-column),var(--grid-width)) 1fr;
	grid-template-rows: 49px;
	align-items: center;
	border-bottom: 1px solid #e4e7eb;
}
.header__bottom {
	display: grid;
	gap: var(--block-spacing) var(--grid-gap);
	grid-template-columns: 1fr repeat(var(--grid-column),var(--grid-width)) 1fr;
	grid-template-rows: 82px;
	align-items: center;
	box-shadow: 0px 1px 1px rgba(53, 53, 53, 0.1);
}
.header__search {
	grid-column: 2/span 4;
	form {
		display: grid;
		grid-template-columns: 1fr auto;
		align-items: center;
		gap: 4px;
		.form-item {
			margin: 0;
		}
		.form-actions {
			position: relative;
			&:before {
				content: "";
				@include sprite($search);
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				margin: auto;
			}
		}
		.form-submit {
			width: 26px;
			height: 26px;
			padding: 0;
			background-color: transparent;
			border: none;
			&:focus,
			&:hover {
				border: none !important;
			}
		}
	}
}
.header__location {
	grid-column: 6/span 4;
	display: flex;
	align-items: center;
	justify-self: flex-end;
	gap: 12px;
	&:before {
		content: "";
		@include sprite($location);
		display: block;
	}
}
.header__email {
	grid-column: 10/span 2;
	display: flex;
	align-items: center;
	justify-self: flex-end;
	gap: 12px;
	border: none;
	&:before {
		content: "";
		@include sprite($email);
		display: block;
	}
}
.header__phone {
	grid-column: 12/span 2;
	text-align: right;
	display: flex;
	align-items: center;
	justify-self: flex-end;
	gap: 12px;
	border: none;
	&:before {
		content: "";
		@include sprite($phone);
		display: block;
	}
}
.header__logo {
	grid-column: 2/span 2;
}
.header__menu {
	margin: 0;
	padding: 0;
	grid-column: 4/span 10;
	display: flex;
	justify-content: flex-end;
	gap: rem(48px);
	@include max-screen(1280px) {
		gap: rem(32px);
	}
	li {
		margin: 0;
		&:before {
			display: none;
		}
	}
	a {
		font-size: var(--font-size-sm);
		font-weight: 600;
		border: none;
	}
}